import { createSlice} from '@reduxjs/toolkit';

export const educationSlice = createSlice({
  name: 'education',
  initialState: {
    activeModul: 0,
    activeWord: 0,
    moduls:[
      {
      words: [
       {content: "plane", translation: "samolot",img : "data:image/jpeg;base64,"},
      {content: "car", translation: "auto",img : "data:image/jpeg;base64,"},
      {content: "bus", translation: "autobus", img:"data:image/jpeg;base64,"},
      { content : "train", translation : "pociąg", img : "data:image/jpeg;base64," },
      { content : "boat", translation : "łódź", img : "data:image/jpeg;base64,"}
      ]},
      {
        words: [
      {content: "face", translation: "twarz"},
      {content: "nose", translation: "nos"}
        ]
      }
    ]
    }
    ,
    reducers: {
      next: state => {
      state.activeWord = state.activeWord === state.moduls[state.activeModul].words.length -1
       ? 0
       : state.activeWord += 1 ;
        
      },
      prev: state => {
       state.activeWord = state.activeWord === 0 
       ? state.moduls[state.activeModul].words.length-1  
       : state.activeWord -= 1;
      },
      changeActiveModul: (state,action) => {
        
        state.activeModul = action.payload;
        state.activeWord = 0;
      }
  }
})
export const {next,prev,changeActiveModul} = educationSlice.actions;

export const selectWords =  state => state.education.moduls[state.education.activeModul].words;
export const selectActiveWord = state => state.education.activeWord;
export const selectActiveModul = state =>  state.education.activeModul;

export default educationSlice.reducer;